document.addEventListener('DOMContentLoaded', () => {

  const menuBtns = document.querySelectorAll('.dropdown-btns');
  const drops = document.querySelectorAll('.dropdown');

  menuBtns.forEach(el => {
    el.addEventListener('click', (e) => {
      let currentBtn = e.currentTarget;
      let drop = currentBtn.closest('.dropdown-item').querySelector('.dropdown');

      menuBtns.forEach(el => {
        if (el !== currentBtn) {
          el.classList.remove('dropdown-btn--active')
        }
      });

      drops.forEach(el => {
        if (el !== drop) {
          el.classList.remove('dropdown--active');
        }
      });

      drop.classList.toggle('dropdown--active');
      currentBtn.classList.add('dropdown-btn--active');
    });
  });

  document.addEventListener('click', (e) => {
    if (!e.target.closest('.characteristics__list')) {
      menuBtns.forEach(el => {
        el.classList.remove('dropdown-btn--active')
      });

      drops.forEach(el => {
        el.classList.remove('dropdown--active');
      });
    }
  });
});

