document.addEventListener('DOMContentLoaded', () => {
  const tabs2 = document.querySelector('.tabs2');
  const serviceBtn = document.querySelectorAll('.service__btn');
  const serviceContnet = document.querySelectorAll('.service__content');

  if (tabs2) {
    tabs2.addEventListener('click', (e) => {
      if (e.target.classList.contains('service__btn')) {
        const tabsPath = e.target.dataset.tabsPath;
        tabsHandler(tabsPath);
      }
    });
  }

  const tabsHandler = (path) => {
    serviceBtn.forEach(el => {el.classList.remove('service__btn--active')});
    document.querySelector(`[data-tabs-path="${path}"]`).classList.add('service__btn--active');

    serviceContnet.forEach(el => {el.classList.remove('service__content--active')});
    document.querySelector(`[data-tabs-target="${path}"]`).classList.add('service__content--active');
  };
});
